.todo-container{
    height: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    padding-top: 50px;
}

.title{
    font-size: 1.6rem;
    margin-bottom: 10px;
}

.form-container{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;

}

.form-container .form{
    height: 100%;
    width: 500px;
    border: 1px solid steelblue;
    margin-bottom: 20px;
}

.todo-container .form .todo-input{
    border-style: none;
    outline: none;
    width: 70%;
    padding: 5px;
}

.form-container .form .add-todo-btn{
    width: 30%;
    background-color: steelblue;
    color: white;
    border-style: none;
    cursor: pointer;
    padding: 5px 10px;
}

table{
    border-collapse: collapse;
    width: 80%;
}

tbody:nth-child(even){
    background-color: #EBF5FB;
}

tbody:nth-child(odd){
    background-color: #FDEDEC;
}

.todo-heading{
    width: 80%;
    height: 40px;
    border:1px solid rgb(211, 235, 255);
}

.todo-sub-heading{
    width: 20%;
    height: 40px;
    border:1px solid rgb(211, 235, 255);
}

td{
    height: 40px;
    border: 1px solid white;
}

.edit-todo-btn, 
.delete-todo-btn,
.update-todo-btn,
.cancel-todo-btn{
    height: 100%;
    width: 50%;
    border-style: none;
    cursor: pointer;
    color: white;
}

.delete-todo-btn,
.cancel-todo-btn{
    background-color: tomato;
}

.edit-todo-btn,
.update-todo-btn{
    background-color: steelblue;
}

.edit-input{
    height: 100%;
    width: 100%;
    padding: 5px;
    border-style: none;
    border-bottom: 1px solid red;
    outline-color: red;
}


